// Animations

$bouncy: cubic-bezier(.87,-.41,.19,1.44);

@keyframes auth {
    from {
        @include opacity(0);
        transform: translateX(-50%) scale(0.9); }
    to {
        @include opacity(1);
        transform: translateX(-50%) scale(1); } }

@keyframes left-in {
    from {
        @include opacity(0);
        transform: translateX(5rem); }
    to {
        @include opacity(1);
        transform: translateX(0); } }

@keyframes up-in {
    from {
        @include opacity(0);
        transform: translateY(-2rem); }
    to {
        @include opacity(1);
        transform: translateY(0); } }

@keyframes show-modal {
    from {
        @include opacity(0); }
    to {
        @include opacity(1); } }
