@keyframes auth {
  from {
    visibility: hidden;
    filter: alpha(opacity=0);
    opacity: 0;
    transform: translateX(-50%) scale(0.9);
  }
  to {
    visibility: visible;
    filter: alpha(opacity=100);
    opacity: 1;
    transform: translateX(-50%) scale(1);
  }
}

@keyframes left-in {
  from {
    visibility: hidden;
    filter: alpha(opacity=0);
    opacity: 0;
    transform: translateX(5rem);
  }
  to {
    visibility: visible;
    filter: alpha(opacity=100);
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes up-in {
  from {
    visibility: hidden;
    filter: alpha(opacity=0);
    opacity: 0;
    transform: translateY(-2rem);
  }
  to {
    visibility: visible;
    filter: alpha(opacity=100);
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes show-modal {
  from {
    visibility: hidden;
    filter: alpha(opacity=0);
    opacity: 0;
  }
  to {
    visibility: visible;
    filter: alpha(opacity=100);
    opacity: 1;
  }
}

