.layer {
  visibility: hidden;
  filter: alpha(opacity=0);
  opacity: 0;
  background: rgba(0, 0, 0, 0.7);
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 99;
}

.layer.show {
  visibility: visible;
  filter: alpha(opacity=100);
  opacity: 1;
  animation: show-modal .3s forwards ease-in-out;
}

.layer.show .modal {
  visibility: visible;
  filter: alpha(opacity=100);
  opacity: 1;
  transform: translate(-50%, 10%);
  transition: all 0.3s cubic-bezier(0.87, -0.41, 0.19, 1.44);
}

.layer .modal {
  visibility: hidden;
  filter: alpha(opacity=0);
  opacity: 0;
  background: #fff;
  border-radius: 0.2142857143rem;
  box-shadow: 0 2px 5px 3px rgba(0, 0, 0, 0.1);
  left: 50%;
