// Table

.table {
    border-collapse: collapse;
    width: 100%;
    thead {
        background: $green;
        th {
            @include font-size(16px);
            color: $white;
            font-weight: 400;
            padding: 1rem;
            text-align: left;
            &:first-child {
                border-top-left-radius: rem(3px); }
            &:last-child {
                border-top-right-radius: rem(3px); } } }
    tfoot {
        border-top: 2rem solid $white; }
    tbody,
    tfoot {
        tr {
            background: $white;
            &:nth-child(odd) {
                background: #f8f8f8; }
            td {
                @include font-size(16px);
                padding: 1rem;
                .product-title {
                    @include font-size(16px);
                    font-weight: bold; }
                .product-category,
                .product-brand {
                    @include font-size(14px); }
                .link {
                    color: $blue;
                    text-decoration: none;
                    &:hover {
                        text-decoration: underline; } }
                .input {
                    &.qty,
                    &.custom-price {
                        @include font-size(16px);
                        background: $white;
                        border: 1px solid $gray;
                        border-radius: rem(3px);
                        color: $dark;
                        font-family: $sans;
                        padding: .5rem; }
                    &.qty {
                        width: rem(70px); }
                    &.custom-price {
                        width: rem(100px); } }
                .delete-row {
                    background: none;
                    border: 0;
                    color: $red;
                    font: inherit;
                    &:hover {
                        cursor: pointer; } }
                .badge {
                    @include font-size(13px);
                    border-radius: rem(3px);
                    color: $white;
                    display: inline-block;
                    padding: .3rem .5rem;
                    text-align: center;
                    &.discount {
                        @include font-size(18px); }
                    &.badge-green {
                        background: $success; }
                    &.badge-blue {
                        background: $blue; }
                    &.badge-yellow {
                        background: $yellow; }
                    &.badge-red {
                        background: $red; } }
                .price {
                    @include font-size(17px);
                    color: $greener;
                    font-weight: 400;
                    &.with-sale {
                        text-decoration: line-through; } }
                .dropdown {
                    @include font-size(20px);
                    color: inherit;
                    display: block;
                    position: relative;
                    &:hover {
                        .list {
                            @include opacity(1); } }
                    .list {
                        @include opacity(0);
                        background: $white;
                        border-radius: rem(3px);
                        box-shadow: 0 2px 5px 3px rgba($black, 0.1);
                        left: 0;
                        list-style: none;
                        overflow: hidden;
                        position: absolute;
                        top: 0;
                        z-index: 10;
                        .item {
                            .link {
                                @include font-size(16px);
                                background: none;
                                border: 0;
                                color: inherit;
                                display: block;
                                font-family: inherit;
                                padding: .5rem .8rem;
                                text-align: left;
                                text-decoration: none;
                                width: 100%;
                                &:hover {
                                    background: rgba($black, 0.1);
                                    cursor: pointer; } } } } }
                .product-photo {
                    border-radius: rem(3px);
                    height: rem(100px);
                    overflow: hidden;
                    position: relative;
                    width: rem(100px);
                    .img {
                        position: relative;
                        top: 50%;
                        transform: translateY(-50%);
                        width: 100%; } } } } } }
.empty {
    text-align: center;
    .typcn {
        @include font-size(80px);
        font-weight: 400; }
    .title {
        @include font-size(28px);
        font-weight: 400;
        margin-bottom: 3rem; } }
.pagination {
    text-align: center;
    .list {
        @include clear;
        display: inline-block;
        list-style: none;
        .item {
            float: left;
            margin-right: .5rem;
            .link,
            span {
                @include font-size(14px);
                border-radius: rem(3px);
                display: inline-block;
                font-family: $sans;
                font-weight: 400;
                padding: .4rem .8rem;
                text-align: center;
                text-decoration: none; }
            .link {
                background: $green;
                color: $white; }
            span {
                background: $gray; } } } }
